#ugolkrug{
 border: 3px #CCCCCC solid;
 -moz-border-radius: 50px; /* закругление для старых Mozilla Firefox */
 -webkit-border-radius: 50px; /* закругл. для старых Chrome и Safari */
 -khtml-border-radius: 50px; /* закругл.для брауз.Konquerer сист. Linux */
 border-radius: 50px; /* закругление углов для всех, кто понимает */
 padding: 5px; /* внутренние отступы */
 height:50px; /* Высота фигуры */
}

a, div, img, p, button {
    user-select: none;
    -webkit-user-select: none;
    -webkit-touch-callout: none;
    -webkit-tap-highlight-color: transparent;

    -moz-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    -o-user-select: none;
    user-select: none;
}

.nocopy {
    user-select: none;
    -webkit-user-select: none;
    -webkit-touch-callout: none;
    -webkit-tap-highlight-color: transparent;

    -moz-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    -o-user-select: none;
    user-select: none;
}

